const $win = $(window);
const $section = $('.faq-page h3');
let sectionsObj = {};

function calculateOffsets(){
  $section.each(function(e) {
    const $this = $(this);
    const thisId = $this.attr('id');
    sectionsObj[thisId] = $this.offset().top - $('.header__inner').outerHeight()
  });
}


function scrollSpy() {
  const $sidebar = $('.sidebar');
  const scrollTop = $win.scrollTop();
  let interval;
  let hasSection = true;
  calculateOffsets();
  for (let i in sectionsObj) {
    if (sectionsObj[i] > scrollTop) {
      let $target = $sidebar.find('a[href*=' + i + ']').parent();
      hasSection = true;

      if ($target.prev().length) {
        $target = $target.prev();
      }

      clearInterval(interval);
      interval = setTimeout(function() {
        $sidebar.find('.active').removeClass('active');
        $target.addClass('active');
      }, 10);
      break;
    } else {
      hasSection = false;
    }
  }

  if (!hasSection) {
    let $target = $sidebar.find('li:last-child a').parent();
    $sidebar.find('.active').removeClass('active');
    $target.addClass('active');
  }
}

$win.on('scroll load', function() {
  if ($win.width() >= 768) {
    scrollSpy();
  } else {
    hideUnactive();
  }
});

$('.faq-page .sidebar a').on('click', function(e) {
  e.preventDefault();
  const $this = $(this);
  const thisHref = $this.attr('href');

  if ($win.width() < 768) {
    $this
      .parent()
      .addClass('active')
      .siblings()
      .removeClass('active');
    $this.closest('ul').toggleClass('show');
    hideUnactive();
  } else {
    $('html,body').animate({
      scrollTop: $(thisHref).offset().top - $('.header__inner').outerHeight()
    });
  }
});

function hideUnactive() {
  const $activeEle = $('.faq-page .sidebar ul .active a');
  const eleHref = $activeEle.attr('href');
  if ($activeEle.length) {
    const eleTarget = eleHref.replace('#', '');
    $('.faq-page .qa-row').addClass('hide');
    $('.faq-page')
      .find(`.qa-row[data-heading="${eleTarget}"]`)
      .removeClass('hide');
  }
}



