$('.video .play-icon').on('click', function() {
  const $this = $(this);
  const $parent = $this.parent();
  const $iframe = $parent.find('iframe');
  $parent.addClass('clicked');
  const player = new YT.Player('video-player',{
    events: {
      onReady: function(event) {
        event.target.playVideo()
      }
    }
  });
})



