import { updateHtml } from '../header/mini-cart';
import { toastMessage } from '../general/toaster';

const $body = $('body');
const $customSelect = $('.custom-select-wrapper');
const $currentSelectEle = $customSelect.find('.current');
const $fixedForm = $('.fixed-form ');
const $productForm = $('.product__entry form');

$currentSelectEle.on('click', function() {
  $customSelect.toggleClass('clicked');
})

$('.custom-select__dropdown input').on('change', function() {
  const $this = $(this);
  const radioValue = $this.val();
  const labelText = $this.parent().find('label').text();
  const thisIndex = Shopify.subscribe_intervals.indexOf(radioValue.toString());
  if (radioValue === 'onetime') {
    const $oneTimeeRadio = $('.rc_radio[value="onetime"]');
    $oneTimeeRadio.prop('checked', true).trigger('change').trigger('click');
  } else {
    const $radioDeliver = $('.rc_radio[value="autodeliver"]');
    $radioDeliver.prop('checked', true).trigger('change').trigger('click');
    $('#rc_radio_options .selectric-items li:eq(' + thisIndex + ')').trigger('click');
  }

  $currentSelectEle.text(labelText);
  $customSelect.removeClass('clicked');
})

$fixedForm.find('input[name="quantity"]').on('change', function(){
  $productForm.find('input[name="quantity"]').val($(this).val())
})


$('.ajax-add').on('click', function(e){
  e.preventDefault();
  $.ajax({
    url: $productForm.attr('action'),
    type: 'POST',
    data: $productForm.serialize(),
  })
  .done((data) => {
    updateHtml($(data), false)
    $body.addClass('timeout');
    // setTimeout(() => {
    //   $body.addClass('slideout');
    // }, 2000);
    // setTimeout(() => {
    //   // $body.removeClass('timeout slideout');
    // }, 2500);
  })
  .fail(() => {
    toastMessage(Shopify.theme.locale.cart.error);
  })
});




