import { is_touch_device } from '../general/touch';

const $header = $('.header');
const $body = $('body');
const $html = $('html');
const $mainDropDown = $('.menu__dropdown--main');


$header.find('.nav-trigger').on('click', function(e) {
  e.preventDefault();
  $(this).find('a').toggleClass('active');
  $body.add($html).toggleClass('nav-visible');
})

$('.main-nav > li > a').on('mouseenter', function() {
  if (!is_touch_device()) {
    const $this = $(this);

    $mainDropDown.not($this.next()).removeClass('show');
    $this.next('.menu__dropdown--main').addClass('show');
  }
})

// $('.navigation a[data-target]').on('mouseenter', function() {
//   if (!is_touch_device()){
//     const $this = $(this);
//     const target = $this.data('target');
//     $(`.products-dropdown[data-item="${target}"]`).addClass('active').siblings().removeClass('active');
//   }
// })

$mainDropDown.on('mouseleave', function() {
  $mainDropDown.removeClass('show');
})

$('.navigation .has-dropdown').on('click', function(e) {
  if (is_touch_device()) {
    e.preventDefault();
    const $this = $(this);
    
    $mainDropDown.not($this.next()).removeClass('show');
    $this.next('.menu__dropdown--main').toggleClass('show');

    $('.navigation a').not($this).not($this).removeClass('clicked');
    $this.toggleClass('clicked');
  }
})


// $('.navigation a[data-target]').on('click', function(e) {
//   if (is_touch_device() && $(window).width() >= 768) {
//     e.preventDefault();
//     const $this = $(this);
//     const target = $this.data('target');
//     $('.navigation a').not($this).removeClass('clicked');
//     $this.toggleClass('clicked');
//     $(`.products-dropdown[data-item="${target}"]`).toggleClass('active').siblings().removeClass('active');
//   }
// })

$(document).on('mouseup', function (e){
  var container = $(".header");

  if (!container.is(e.target) // if the target of the click isn't the container...
      && container.has(e.target).length === 0) // ... nor a descendant of the container
  {
      $mainDropDown.removeClass('show');
  }
});



