const $win = $(window);

function responsiveImage(){
  $('[data-img-mobile]').each(function() {
    const $this = $(this);
    const mobileImage = $this.data('img-mobile');
    const desktopImage = $this.data('img-desktop');

    if ($win.width() < 1024) {
      $this.attr({
        'src': mobileImage
      })
    } else {
      $this.attr({
        'src': desktopImage
      })
    }
  })
}

responsiveImage();

$win.on('resize', function() {
  responsiveImage();
})



