import { is_touch_device } from './touch';

$('.accordion__title').on('click', function() {
  const $this = $(this);
  const $accordion = $this.closest('.accordion');
  const $content = $accordion.closest('.product-content');
  const $accordion_content = $accordion.find('.accordion__content');
  const contentHeight =
    $content.height() -
    $content.find('h3').outerHeight(true) -
    $content.find('.btn').outerHeight() -
    $this.outerHeight() -
    parseInt($accordion.css('margin-bottom'));

  if ($accordion.hasClass('active')) {
    $accordion_content.animate({
      height: 0
    });
  } else {
    $accordion_content.animate({
      height: contentHeight
    });
  }
  $accordion_content.css({
    'max-height': contentHeight
  });
  $accordion.toggleClass('active');

  if (is_touch_device() && $(window).width() < 768) {
    $accordion.toggleClass('fixed');
  } else {
  }
});

$('.faq-page .question').on('click', function() {
  const $this = $(this);
  const $parent = $this.parent();
  $parent.toggleClass('active');
  $parent
    .find('.answer')
    .stop(true)
    .slideToggle();
  $('.faq-page .answer')
    .not($parent.find('.answer'))
    .slideUp();
  $('.qa-row')
    .not($parent)
    .removeClass('active');
});

$('.faq-page .content h3').on('click', function() {
  const $this = $(this);
  const dataTarget = $this.attr('id');
  $(`.qa-row[data-heading="${dataTarget}"]`).add($this).toggleClass('show');
})



