import { updateHtml } from '../header/mini-cart';
import { toastMessage } from '../general/toaster';

const $body = $('body');

$('form[action="/cart/add"]').on('submit', function(e){
  e.preventDefault();
  const $form = $(this);
  $form.addClass('loading');

  $.ajax({
    url: $form.attr('action'),
    type: 'POST',
    data: $form.serialize(),
  })
  .done((data) => {
    $form.removeClass('loading');
    if ($body.hasClass('template-product')) {
      
      if (!$('.fixed-form').hasClass('visible')) {
        $body.addClass('full-height')  
      }
      $body.addClass('timeout');
      setTimeout(() => {
        // $body.addClass('slideout');
      }, 2000);
      setTimeout(() => {
        // $body.removeClass('timeout slideout full-height');
      }, 2500);
      updateHtml($(data), false);
    } else {
      updateHtml($(data));
    }

  }).fail((e) => {
    $form.removeClass('loading');
    toastMessage(Shopify.theme.locale.cart.error);
  })
})



