$('.accordion-section .side-text').on('click', function() {
  const $item = $(this).closest('.accordion-item ');
  $item.addClass('active').siblings().removeClass('active');
})

$(document).on('shopify:section:load', function(){
  $('.accordion-section .side-text').on('click', function() {
    const $item = $(this).closest('.accordion-item ');
    $item.addClass('active').siblings().removeClass('active');
  })
})



