const $win = $(window);
const $productForm = $('.product__form');
const $fixedBar = $('.fixed-form');

function fixedBar() {
  if (!$('body').hasClass('template-product')) {
    return;
  }
  const winScrollTop = $win.scrollTop();
  const productBottomLine = $productForm.offset().top + $productForm.height();
  if (winScrollTop > productBottomLine) {
    $fixedBar.addClass('visible');
  } else {
    $fixedBar.removeClass('visible');
  }
}

$win.on('load resize scroll', function() {
  fixedBar();
})

$('.fixed-form .js-fixed-form').on('click', function(e) {
  e.preventDefault();
  $('.product__container .product__form').addClass('fixed');
})

$('.product__form .subscribe-holder .close-btn').on('click', function () {
  $('.product__form.fixed').removeClass('fixed');
})



