const $win = $(window);
const $header = $('.header');
const offSet = $('.header__bar').outerHeight();

$win.on('scroll load', function(){
  if ($win.scrollTop() >= offSet) {
    $header.find('.header__inner').addClass('fixed')
  } else {
    $header.find('.header__inner').removeClass('fixed')
  }
})



