import 'slick-carousel';
import { lastSlideItem } from '../home/last-slide-item';

const $win = $(window);

function forceOpacity() {
  return setInterval(function() {
    lastSlideItem()
  }, 10) 
}

const $slider = $('#insta-feed');
if ($slider.length) {
  var config = { childList: true};

  $slider.on('init afterChange', function() {
    lastSlideItem()
    const interval = setInterval(function() {
      lastSlideItem()
    }, 20) ;
    setTimeout(function() {
      clearInterval(interval);
    }, 200)
  })

  // Callback function to execute when mutations are observed
  var callback = function(mutationsList, observer) {
    if ($slider.children().length > 1) {
      $slider.append('<div class="slide-items"></div>')
      $slider.children().each(function() {
        const $this = $(this);
        if ($this.hasClass('instafeed-lightbox')) {
          const $img = $this.prev().find('.js-lazy-image');
          const dataImg = $img.data('src');
          $img.attr('src', dataImg);
          $img.addClass('js-lazy-image--handled');
          $slider.find('.slide-items').append($this.prev())
        }
      });

      $slider.find('.slide-items').slick({
        slidesToShow: 4,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        variableWidth: true,
        swipe: false,
        draggable: false
      })

      observer.disconnect();
    }

  };
  var observer = new MutationObserver(callback);
  observer.observe($slider[0], config);

  $win.on('resize', function() {
    lastSlideItem();
  })
}

if ($win.width() < 768) {
  const $accordion = $('.accordion-section .flex-row');

  $accordion.on('beforeChange', function(event, slick, currentSlide, nextSlide){
    const $nextSlide = $(slick.$slides[nextSlide]);
    const slideHeight = $nextSlide.find('.accordion-item').height();
    $accordion.find('.slick-list').animate({
      height: slideHeight
    })
  });

  $accordion.slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: false,
    arrows: false,
    cssEase: 'linear',
    speed: 200,
    infinite: true
  })

  $('.arrow-prev').on('click', function() {
    $accordion.slick('slickPrev')
  })

  $('.arrow-next').on('click', function() {
    $accordion.slick('slickNext')
  })
}



