import { is_touch_device } from './touch';

const $win = $(window);

$('select[name="address[country]"]').each(function() {
  const $this = $(this);
  const thisID = $this.attr('id');
  const $province = $this.closest('form').find('select[name="address[province]"]');
  const provinceID = $province.attr('id');
	const provinceHolderID = $province.closest('.form__row').attr('id');

  new Shopify.CountryProvinceSelector(thisID, provinceID, {
    hideElement: provinceHolderID
  });

  $this.on('selectric-change', function() {
    const provinces = $(this)
      .find('option:selected')
      .data('provinces');
    let newOptions = '';

    if (provinces.length) {
      provinces.map(provice => {
        newOptions += `<option value="${provice[0]}">${provice[0]}</option>`;
      });
    }

    $province.closest('.form__row').hide();

    if (newOptions !== '') {
      $province.closest('.form__row').show();
      $province.html(newOptions);
    }

    $province.selectric('refresh');
  });
  $province.selectric('refresh');
});

const $mainAddButton = $('.btn--new-main');
$mainAddButton.on('click', function(e){
	e.preventDefault();
	$(this).addClass('hidden');
})

$('.form--address .btn--new').on('click', function(e){
	e.preventDefault();
	$mainAddButton.removeClass('hidden');
})

$('.edit-address-link, .btn--cancel').on('click', function(e){
	$(this).closest('.address').toggleClass('editable-address').closest('.section__content').toggleClass('edit-progress')
})

$('.section-account .section__aside li.active a').on('click', function(e){
  if (is_touch_device() && $win.width() < 768) {
    e.preventDefault();
    $(this).closest('ul').toggleClass('visible');
  }
})



