let contactFlag = false;

$('#contact_form').on('submit', function(e){
  const $this = $(this);
  if (!contactFlag) {
    e.preventDefault();
    $.ajax({
      url: $this.attr('action'),
      type: $this.attr('method'),
      data: $this.serialize(),
    })
    .done((data) => {
      const $html = $(data);
      $html.find('.form__message--success').appendTo($this)
    })
    .fail((data) => {
      contactFlag = true;
      $this.trigger('submit');
    })
  }
})



