const $win = $(window);

function lastSlideItem() {
  const winWidth = $win.width();
  let $lastItem = false;

  $('.community .slick-slide').removeClass('opacity');

  $('.community .instafeed-container').each(function() {
    const $this = $(this);
    const rightLine = $this.offset().left + $this.width();
    if (rightLine > winWidth && !$lastItem) {
      $lastItem = $this;
    }
  })
  if ($lastItem.length) {
    $lastItem.closest('.slick-slide').nextAll().add($lastItem.closest('.slick-slide')).addClass('opacity');
  }
}

module.exports = {
  lastSlideItem
}



