import 'selectric';

function customSelect() {
  $('.rc_select.rc_select__frequency').selectric({
    labelBuilder: 'Delivery every {text}',
    optionsItemBuilder: '<span class="icon-radio"></span> {text}',
  });
}

if (Shopify.product_subscribe) {
  const checkExist = setInterval(function() {
    if (typeof ReCharge === 'object') {
      customSelect();
      clearInterval(checkExist);
    }
  }, 100);
}

const $customSelect = $('.custom-select-wrapper');
const $currentSelectEle = $customSelect.find('.current');
$('#rc_container .rc_radio').on('change', function() {
  const $this = $(this);
  const radioValue = $this.val();
  let $targetInput;
  if (radioValue === 'onetime') {
    $targetInput = $customSelect.find(`.custom-select__dropdown ul input[value=${radioValue}]`);
  } else {
    const selectValue = $('.rc_select__frequency').val();
    $targetInput = $customSelect.find(`.custom-select__dropdown ul input[value=${selectValue}]`);
  }
  const labelText = $targetInput.closest('li').find('label').text();
  $targetInput.prop('checked', true);
  $currentSelectEle.text(labelText);
})

$('.product__form').on('change', '.rc_select__frequency', function() {
  const $select = $(this);
  const $targetInput = $customSelect.find(`.custom-select__dropdown ul input[value=${$select.val()}]`);
  const labelText = $targetInput.closest('li').find('label').text();
  $targetInput.prop('checked', true);
  $currentSelectEle.text(labelText);
})



