function toastMessage(message){
  $(`<div class="toast-message"><span class="toast-message__close"></span><p>${message}</p></div>`).appendTo('body');

  const $container = $('.toast-message');
  jQuery('body').on('click', '.toast-message__close', function() {
    $container.removeClass('reveal');
  })

  setTimeout(() => {
    $container.addClass('reveal');
  }, 100)

  setTimeout(() => {
    $container.removeClass('reveal');
  }, 2000)

  setTimeout(() => {
    $container.remove();
  }, 2300);
}

module.exports = {
  toastMessage
}



