import { is_touch_device } from '../general/touch';

$('.filter-dropdown li a').on('click', function(e){
  if (is_touch_device() && $(window).width() < 768) {
    e.preventDefault();
    const $this = $(this);
    const dataTag = $this.parent().data('tag');

    if (dataTag === '*') {
      $this.parent().siblings().removeClass('active');
    } else {
      $this.closest('ul').find('[data-tag="*"]').removeClass('active');
    }
    $this.parent().toggleClass('active');
  }
})

$('.js-apply-filters').on('click', function(e) {
  e.preventDefault();
  const $this = $(this);
  const $dropDown = $this.closest('.filter-dropdown');
  let collectionUrl = $this.data('collection');
  let tagArr = [];
  $dropDown.find('li.active a').each(function () {
    const $link = $(this);
    const dataTag = $link.parent().data('tag');
    if (dataTag !== '*') {
      tagArr.push(dataTag);
    }
  })

  window.location = `${collectionUrl}/${tagArr.join('+')}`
})



