import { updateHtml } from '../header/mini-cart';
const $formCart = $('.form--cart');

$('.main').on('change', '.form--cart .qty input', function(e) {
  const $this = $(this);
  const $form = $this.closest('.form--cart');

  $form.addClass('loading');
  $.ajax({
    url: '/cart/update.js',
    type: 'POST',
    dataType: 'json',
    data: $this.attr('name') + '=' + $this.val(),
  })
  .done((update) => {
    $formCart.find('.form__actions .total_row .price').text(Shopify.formatMoney(update.total_price));
    
    if($this.val() <= 0){
      $this.closest('tr').remove();
    }
    
    $.ajax({
      url: '/cart',
      dataType: 'html',
    })
    .done((data) => {
      const $html = $(data);
      updateHtml($html, false);

      $formCart.find('.shipping-text').html($html.find('.form--cart .form__body .shipping-text').html());

      if (update.item_count <= 0){
        $formCart.html($html.find('.form--cart').html());
      }
    }).always(() => {
      $form.removeClass('loading');
    })
  });
})



