// Login / Forgotten password toggle
$(".forgotten-pass, .loginfrom-link").on("click", function(e) {
	e.preventDefault();
	const $this = $(this);
	const targetString = $this.attr("href").replace("#", "");
	$(`.${targetString}`).show();
	$this
		.closest("form")
		.parent()
		.hide();
});

// Address new
$(".template-customer--addresses .btn--new, .form--address-new .btn--cancel").on("click", function(e) {
	e.preventDefault();
	const $this = $(this);
	const targetString = $this.attr("href").replace("#", "");
	$(`.${targetString}`).toggle();
});

// Collection sortBy
$("#sortBy").on("change", function() {
	Shopify.queryParams = {};
	const $this = $(this);
	const thisVal = $this.val();

	if (location.search) {
		const getArgumentsArray = location.search.substr(1).split("&");
		getArgumentsArray.map(function(ele) {
			const eleArray = ele.split("=");
			Shopify.queryParams[decodeURIComponent(eleArray[0])] = decodeURIComponent(eleArray[1]);
		});
	}

	Shopify.queryParams.sort_by = thisVal;
	location.search = jQuery.param(Shopify.queryParams);
});

// Product price update
$(".product-single__variants").on("change", function() {
	const $this = $(this);
	const thisPrice = $this.find("option:selected").data("price");
	$(".product__price").html(thisPrice);
});



