const $win = $(window);
const $header = $('.header');
const $miniCart = $('.mini-cart');
const $cartBag = $('.util-cart a');

function updateHtml($html, reveal = true) {
  $cartBag.html($html.find('.util-cart a').html());
  $miniCart.html($html.find('.mini-cart').html());

  $miniCart.removeClass('loading');
  if (reveal) {
    $miniCart.addClass('show');
  }
}

function updateMiniCart(callback) {
  $.ajax({
      url: '/cart',
    })
    .done((data) => {
      updateHtml($(data))
    })
}

$win.on('scroll', function () {
  const headerHeight = Object.values(document.querySelectorAll('.header__bar, .header__inner')).reduce((acc, val) => {
    acc += val.clientHeight;
    return acc;
  }, 0);

  if ($win.scrollTop() <= headerHeight) {
    $miniCart.css({
      "top": headerHeight - $win.scrollTop()
    })
  } else {
    if ($('.fixed-form').length > 0) {
      if ($('.fixed-form').hasClass('visible')) {
        $miniCart.css({"top": $('.fixed-form.visible').outerHeight(true)})
      } else {
        $miniCart.css({"top": 0})
      }
    } else {
      $miniCart.css({"top": 0})
    }
  }
})

$miniCart.on('click', '.js-add, .js-remove', function (e) {
  e.preventDefault();
  const $this = $(this);

  $miniCart.addClass('loading');

  $.ajax({
      url: $this.attr('href'),
    })
    .done((data) => {
      updateHtml($(data))
    })
}).on('click', '.mini-cart__close', function () {
  $miniCart.removeClass('show');
  $('body').removeClass('full-height timeout');
})

$miniCart.on('change', '.qty input', function () {
  const $this = $(this);
  $miniCart.addClass('loading');

  $.ajax({
      url: '/cart/update.js',
      type: 'POST',
      dataType: 'json',
      data: $this.attr('name') + '=' + $this.val()
    })
    .done(() => {
      updateMiniCart();
    })
})

$('.util-cart a').on('click', function (e) {
  e.preventDefault();
  $miniCart.toggleClass('show');
})

$(document).mouseup(function (e){
	var container = $(".mini-cart ");

	if (!container.is(e.target) && container.has(e.target).length === 0) {
    $miniCart.removeClass('show');
    $('body').removeClass('full-height timeout');
	}
});

module.exports = {
  updateHtml,
  updateMiniCart
}



